.section {
  min-height: 70%;
}
/* .section:nth-child(even) {
  perspective: 900px;
} */
.section-hero {
  height: 100vh;
}
.section-container {
  height: 100%;
}
.section-header-title {
  border: 1px solid rgba(0,0,0,0.15);
  -webkit-box-shadow: 0 5px 20px 1px rgba(0, 0, 0, 0.75);
          box-shadow: 0 5px 20px 1px rgba(0, 0, 0, 0.75);
  border-radius: 15px;
  width: 50%;
  margin: auto;
  margin-bottom: 15px;
}
.section-header h1 {
  text-transform: uppercase;
}
.section-header p {
}
.section-body {}

@media(min-width: 992px) {
  .section-header p {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }
}


